.icon {
  font-size: 10px;
}

.icon:before {
  content: ' ';
  vertical-align: middle;
  display: inline-block;
  background-image: url("../sprite.svg");
  background-repeat: no-repeat;
  background-size: 9.2442em 42.9em;
}

.no-svg .icon:before {
  background-image: url("../sprite.png");
}

.icon.icon-appstore:before {
  background-position: -0.5em -0.5em;
  width: 7.1671000000000005em;
  height: 8.8019em;
}

.icon.icon-blue:before {
  background-position: -0.5em -10.4em;
  width: 5.475200000000001em;
  height: 2.975em;
}

.icon.icon-googleplay:before {
  background-position: -0.5em -14.4em;
  width: 8.2442em;
  height: 8.9269em;
}

.icon.icon-green:before {
  background-position: -0.5em -24.4em;
  width: 3.4156em;
  height: 5.0155em;
}

.icon.icon-red:before {
  background-position: -0.5em -30.5em;
  width: 4.4256em;
  height: 4.4415em;
}

.icon.icon-star:before {
  background-position: -0.5em -36em;
  width: 6.6934em;
  height: 6.3658em;
}
