.lang-swicher_element {
  cursor: pointer;
  padding: 0.2em 1.5em;
  border-radius: 30px;
  opacity: 0.6;
  border: 1px solid transparent;
  margin-right: 0.5em;
  text-transform: uppercase;
  display: inline-block;
}

.lang-swicher_element:hover,
.active {
  text-decoration: none;
  border-color: #ffffff;
  /*opacity: 1;*/
}

.section-h1 {
  font-weight: 700;
  font-size: 3em;
  line-height: 1;
  margin-bottom: 0.5em;
}

.section-h2 {
  margin-bottom: 2em;
  font-size: 1.67em;
  font-weight: 300;
}

.section-h3 {
  margin: 0 0 1em 0;
  font-weight: 100;
  font-size: 2.55em;
  line-height: 1.1em;
}

.three-col .section-h3 {
  margin: 1em 0;
  font-weight: 300;
  font-size: 1.3333em;
}

.font-light {
  font-weight: 300;
}

.font-thin {
  font-weight: 100;
}

.container-flex {
  max-width: 1000px;
  min-width: 320px;
  margin: 0 auto;
  padding: 5em 0;
  display: flex;
  justify-content: space-around;
  align-content: center;
  align-items: center;
}

.item-top {
  align-items: flex-start;
}

.flex-row {
  flex-direction: row;
}

.flex-reverse {
  flex-direction: row-reverse;
}

.flex-col {
  flex-direction: column;
}

.text-block {
  max-width: 25em;
}

.block-inline {
  flex: 1 100%;
}

.flex-wrap {
  flex-wrap: wrap;
}

.three-col {
  max-width: 16em;
  line-height: 1.3em;
}

.lang-swichers {
  overflow: hidden;
  font-size: .7em;
  padding: 1.5em 4em 1.5em;
  justify-content: flex-end;
  display: flex;
  max-width: 1000px;
  min-width: 320px;
  margin: 0 auto;
  text-align: right;
}

.btn-wrapper {
  margin-top: 20px;
  width: 100%;
}

.btn {
  font-size: 0.78em;
  background-color: #ffffff;
  border-radius: 1.56em;
  border: 0;
  cursor: pointer;
  text-decoration: none;
  color: #1ba8f0;
  padding-left: 1.6em;
  padding-right: 1.8em;
  margin-right: 1.1em;
  opacity: 0.9;
  display: inline-block;
  text-align: center;
  line-height: 3;
  margin: 1.1em 1.1em 0.56em 0px;
  min-width: 151px;
}

.btn:hover {
  opacity: 1;
}

.btn:before {
  content: '';
  display: inline-block;
  width: 23px;
  height: 27px;
  vertical-align: middle;
  margin-right: 7px;
  margin-bottom: 3px;
}

.btn.icon:before {
  font-size: 3px;
}

.btn.icon-googleplay {
  margin-right: 0;
}

.icon.icon-googleplay:before {
  background-position: 0em -11.9em;
  width: 8.3em;
  height: 8.8em;
  background-size: 8.1em 36.2em;
}

.icon.icon-appstore:before {
  background-position: 0em 0em;
  width: 7.2em;
  height: 8.7em;
  background-size: 8.2em 36.2em;
}

.header-img {
  margin: 0 0 -12.5em;
  text-align: right;
  position: relative;
  background-size: 100%;
  background-position: 50% 3px !important;
}

.text-center {
  text-align: center;
}

.clear:before,
.clear:after {
  display: table;
  content: '';
}

.clear:after {
  clear: both;
}

.icon-three {
  width: 120px;
  height: 120px;
  line-height: 120px;
  background-color: #ccc;
  border-radius: 50%;
  margin: 0 auto;
  font-size: 10px;
}

