.swiper-container .text-block {
  margin: 0 auto;
  text-align: center;
  margin-bottom: 2.5em;
}

.swiper-container .section-h3 {
  margin-bottom: 0.5em;
}

#slideshow {
  width: 400px;
  min-height: 100px;
  margin: 0 auto;
}

.author {
  margin-top: 0.56em;
}

.star__item {
  display: inline-block;
  vertical-align: middle;
  /*   width: 19px;
    height: 22px;*/
  font-size: 3px;
  overflow: hidden;
  /*background:url('../img/star.svg') no-repeat;*/
}

.icon.icon-star:before {
  background-position: 0em -31em;
  width: 7em;
  height: 6em;
}

.author .stars,
.author .author__name {
  vertical-align: middle;
  display: inline-block;
}

.author .author__name {
  margin-top: 3px;
}

.author__name {
  opacity: 0.8;
  margin-left: 0.56em;
  font-size: 0.78em;
}

.swiper-container {
  background: #d6f0ff;
  background: linear-gradient(to bottom,  #d6f0ff 0%,#ffffff 100%);
}

.img-block,
.header-img {
  text-align: center;
  position: relative;
  background: url("../img/border.svg") no-repeat center center;
  background-size: contain;
}

.header-img .phone-img {
  max-width: 384px;
  padding: 5em 1.2em;
}

/*.phone-border {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 303px;
}*/

.phone-img {
  position: relative;
  z-index: 1;
  max-width: 303px;
  padding: 4.2em 1.1em 4em;
  margin: 0 auto;
}

.footer-mail {
  text-decoration: none;
  color: #a1dbff;
  font-size: 1em;
  margin-top: 2.5em;
  width: 100%;
}

.footer-title {
  text-align: center;
  width: 100%;
  line-height: 1.3;
}

.footer-title h2 {
  font-size: 1.6em;
  font-weight: 100;
  margin: 40px 0 20px;
}

.footer-img-wrapper {
  margin: 0px auto;
}

/*medis query*/

@media only screen and (max-width: 950px) {
  body {
    font: 1em/1.5em 'Roboto', sans-serif;
  }

  .header-img {
    margin-bottom: -17.5em;
    text-align: right;
    max-width: 16em;
  }

  .header-img .phone-img {
    padding: 4em 1em;
  }

  /* .phone-img {
      padding: 4.5em 0.8em;
    }*/
}

@media only screen and (max-width: 800px) {
  .section-h2 {
    display: none !important;
  }

  .three-col {
    max-width: 27em;
    margin: 0em auto 3.2em;
  }

  section img {
    /*max-width: 14em;*/
    height: auto;
  }

  #slideshow {
    max-width: 350px;
  }

  .phone-img {
    padding: 4.2em 0.8em 3.5em;
  }

  .img-block {
    max-width: 224px;
  }
}

@media only screen and (max-width: 690px) {
  body {
    font-weight: 400 !important;
  }

  .container-flex {
    padding: 3em 0;
  }

  .lang-swichers {
    font-size: 1em;
    justify-content: center;
    padding: 1.5em 0 0 0;
    text-align: center;
  }

  .lang-swicher_element {
    padding: 5px 17px;
    border-radius: 30px;
    opacity: .7;
    border: 1px solid transparent;
    margin-right: 3px;
    text-transform: none;
    font-size: 0.8em;
    line-height: 1;
  }

  .lang-swicher_element:hover,
  .active {
    text-decoration: none;
    border-color: transparent;
    opacity: 1;
    background: #0479be;
  }

  .flex-row,
  .flex-reverse {
    /*        flex-direction: column;
        align-items: flex-end;
        flex-wrap: wrap;*/
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
  }

  .header-title {
    text-align: center;
    max-width: 16em;
  }

  #slideshow {
    max-width: 16em;
    min-height: 145px;
  }

  .three-col {
    color: #737373;
  }

  .three-col .section-h3 {
    color: #393939;
    font-weight: 500;
  }

  section {
    background: transparent !important;
    box-shadow: none !important;
  }

  .author .author__name,
  .author .stars {
    display: block;
  }

  .btn {
    width: 100%;
    margin: 0 0 1em;
    font-size: 1em;
    padding: 0;
    opacity: 1;
  }

  .btn-wrapper {
    width: 12em;
    margin: 2.5em auto 2.4em;
  }

  .footer-title h2 {
    font-weight: 300;
  }

  .swiper-container .text-block {
    color: #6c808b;
    min-height: 210px;
  }

  .swiper-container .section-h3 {
    color: #343e43;
    font-weight: 500;
    font-size: 1.333em;
    line-height: 1.333em;
  }

  header {
    background: #00a4f4 !important;
    box-shadow: none !important;
  }

  .footer-mail {
    margin-top: 1.1em;
  }

  .header-img {
    max-width: 16em;
  }
}

@media only screen and (max-width: 550px) {
  .three-col {
    max-width: 20em;
  }

  section img {
    max-width: 10em;
  }

  .phone-img {
    padding: 4em 0.8em 3.5em 0.7em;
  }

  .text-block {
    max-width: 22em;
  }

  .swiper-container .text-block {
    font-size: 15px;
    line-height: 22px;
  }

  .swiper-container .section-h3 {
    font-size: 26px;
  }
}

@media only screen and (max-width: 420px) {
  .three-col {
    max-width: 20em;
    line-height: 1.2em;
    font-size: 15px;
  }

  .three-col .section-h3 {
    font-size: 26px;
  }

  .swiper-container.swiper-container-horizontal > .swiper-pagination {
    top: 230px !important;
  }

  .swiper-container .section-h3 {
    font-size: 1.5em;
    line-height: 1.5em;
  }

  .swiper-container .text-block {
    max-width: 295px;
    min-height: 200px !important;
    line-height: 1.2em;
    font-size: 0.85em;
  }

  #slideshow {
    font-size: 14px;
    line-height: 14px;
    min-height: 85px;
  }
}

@media only screen and (max-width: 321px) {
  #slideshow {
    max-width: 250px;
  }

  .header-img {
    max-width: 16em;
  }

  .lang-swicher_element {
    padding: 4px 12px;
  }
}
